class VPNsStore {
  constructor() {
    makeAutoObservable(this);
    this.vpnService = new VPNService();
  }
  _vpnsData = [];
  _vpnDescr = null;

  get vpnsData() {
    return this._vpnsData;
  }
  get vpnDescr() {
    return this._vpnDescr;
  }

  getVPNsAsync = async () => {
    try {
      const data = await this.vpnService.get('vpns');
      runInAction(() => {
        this._vpnsData = data;
        // let list = [];
        // var i;
        // for (i = 0; i < 20; i++) {
        //   let el = Object.assign({}, data[0]);
        //   el.id = data[0].id + i;
        //   list.push(el);
        // }
        // this._vpnsData = list;
      });
    } catch (error) {
      runInAction(() => {
        this._status = 'error';
      });
    }
  };
  getVPNAsync = async (params) => {
    try {
      const target = 'vpns/' + params;
      const data = await this.vpnService.get(target);
      runInAction(() => {
        this._vpnDescr = data;
      });
    } catch (error) {
      runInAction(() => {
        this._status = 'error';
      });
    }
  };
}

export default new VPNsStore();
